.Content {
    font-size: large;
    color: #ddd;
    grid-area: cont;
    margin-top: 60px;
    margin-bottom: 50px;
    padding-left: 50px;
    padding-right: 50px;
    overflow: auto;
}

.Content div {
    height: auto;
}

@media only screen and (max-width: 600px), screen and (max-height: 400px) {
    .Content {
        margin-top: 10px;
        margin-bottom: 10px;
        padding-left: 10px;
        padding-right: 10px;
    }
}



