.Aside {
    background-color: #df861d;
    color: #dfaa1d;
    grid-area: aside;
}

.Aside-header {
  font-size: 1.5em;
  height: 10%;
  margin-top: 5%;
  margin-bottom: 5%;
  color: #894a00;
  text-align: center;
}

.Aside-buttons {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  font-size: medium;
  height: 80%;
  width: 160px;
  margin-right: 20px;
  margin-left: 20px;
}

.Button {
  border-radius: 20%;
  -webkit-transition: background-color 0.5s;
  -o-transition: background-color 0.5s;
  transition: background-color 0.5s;
  width: 160px;
  height: 75px;
  line-height: 75px;
  text-align: center;
  cursor: pointer;
}

@media only screen and (max-width: 600px), screen and (max-height: 400px) {
  .Aside-header {
    margin: 0;
    padding: 0;
    padding-top: 5px;
    height: 30px;
    font-family: sans-serif;
  }

  .Aside-buttons {
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    font-size: small;
    margin: 0;
    height: 35px;
    padding-bottom: 5px;
    width: 100%;
    font-family: sans-serif;
  }

  .Button {
    width: 50px;
    height: 35px;
    line-height: 35px;
  }
}



