h1 {
  font-size: 1.5em;
  padding-bottom: 20px;
  color: #ff9720;
}

h2 {
  padding-top: 20px;
  padding-bottom: 20px;
  color: #999;
}

h3 {
  padding-top: 20px;
  padding-bottom: 20px;
  color: #df861d;
}

a {
  color: #fdc029;
  text-decoration: none;
}

a:hover {
  color: #894a00;
}

ul {
  list-style: none;
}

li {
  padding-top: 10px;
}

p {
  padding-top: 10px;
}

.Container {
  display: grid;
  grid-template-areas:
    'aside header'
    'aside cont';
  grid-template-rows: 1fr 9fr;
  grid-template-columns: 200px auto;
}

@media only screen and (max-width: 600px), screen and (max-height: 400px) {
  .Container {
    grid-template-areas:
      'aside'
      'header'
      'cont';
    grid-template-rows: 80px 50px auto;
    grid-template-columns: 1fr;
  }
}



