.Header {
    font-size: 3em;
    text-transform: uppercase;
    color: #df861d;
    background-color: #333;
    grid-area: header;
    padding-top: 30px;
  }
  
  .Header-title {
    padding-left: 100px;
  }

  @media only screen and (max-width: 600px), screen and (max-height: 400px) {
    .Header {
      padding-top: 10px;
      font-size: 1.5em;
    }

    .Header-title {
      padding: 0;
      padding-top: 5px;
      text-align: center;
    }
  }
  


