.ScrollPanel {
    height: 350px;
    overflow-x: auto;
    overflow-y: hidden;
    white-space: nowrap;
}
  
.ScrollPanel-item {
  display: inline-block;
  vertical-align: top;
  width: 300px;
  height: 200px;
  padding: 5px;
  margin: 5px;
  margin-top: 60px;
  background-color: #333;
  font-size: 14px;
  top: -300px;
  -webkit-transition: all .3s ease, opacity 2s ease,  top 1s ease;
  -o-transition: all .3s ease, opacity 2s ease,  top 1s ease;
  transition: all .3s ease, opacity 2s ease,  top 1s ease;
  cursor: pointer;
}

.ScrollPanel-item:hover {
  height: 210px;
  padding: 15px;
  -webkit-transform: translateY(-50px);
      -ms-transform: translateY(-50px);
          transform: translateY(-50px);
}

.ScrollPanel-item figure {
  width: 100%;
  height: 100%;
}

.ScrollPanel-item img {
  width: 300px;
  height: 190px;
  border-radius: 0%;
}

.ScrollPanel-item figcaption {
  height: 60px;
  width: 100%;
  padding: 0;
  bottom: 0;
  overflow: hidden;
}

.ScrollPanel-item figcaption p {
  line-height: 25px !important;
  font: bold 12px/18px;
  text-transform: uppercase;
  padding: 0 10px;
  margin: 5px 0;
  width: 100%;
  position: relative;
  background-color: #ddd;
  color: #333;
}

.ScrollPanel-date {
  height: 30px;
  line-height: 30px;
  color: #333;
  font-weight: bold;
  text-align: center;
  border-radius: 1px;
  background-color: #894a00;
  position: relative;
  bottom: 200px;
  -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92);
  transition: -webkit-transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92);
  -o-transition: transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92);
  transition: transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92);
  transition: transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92), -webkit-transform 0.5s cubic-bezier(0.12, 1.6, 0.91, 0.92);
}

.ScrollPanel-item:hover .ScrollPanel-date {
  -webkit-transform: translate3d(0, -30px, 0);
          transform: translate3d(0, -30px, 0);
}
  


